import { getLanguage } from "./language-switcher";

const textData = {
  "startpage__btn--game": ["", "Game"],
  "startpage__btn--results": ["", "Results"],
  "startpage__btn--gallery": ["", "Gallery"],
  "startpage__checkbox-text": [" :", "Language:"],
  game__questions: [":", "Question:"],
  score__text: [":", "Score:"],
  footer__info: ["   2022", "Vadim Monastyrskiy  2022"],
};

function fillElemText(key) {
  const isRussian = getLanguage() === "ru" ? 0 : 1;

  document.querySelector(`.${key}`).textContent = textData[key][isRussian];
}
//
export function fillTextContent() {
  for (let key in textData) {
    fillElemText(key);
  }
}
